<script>
import router from '../router/index';
export default {
    name: 'General-form',
    data() {
        return {
            valid:true,
            optSkills:['Numérica', 'Programación', 'Análisis', 
            'Bases De Datos', 'Redes', 'Comunicaciones', 'Sistemas Operativos'
            ],
            validationField:{
                nameRules:[
                    v => !!v || 'Nombres es requerido'
                ],
                last_nameRules:[
                    v => !!v || 'Apellidos es requerido'
                ],
                DUIRules: [
                    v => !!v || 'DUI es requerido',
                    v => (v && v.length >= 10) || 'Número de DUI debe tener un minimo de 10 caracteres',   
                ],
                NITRules:[
                    v => !!v || 'NIT es requerido',
                    v => (v && v.length >= 17) || 'Número de NIT debe tener un minimo de 17 caracteres',
                ],
                profession:{
                    name_professionRules:[ v => !!v || 'Profesión es requerido'],
                    institutionRules:[v => !!v || 'Institución es requerido'],
                    yearRules: [v => !!v || 'Año es requerido']
                },
                mastery:{
                    name_masteryRules:[v => !!v || 'Maestría es requerido'],
                    institutionRules:[v => !!v || 'Institución es requerido'],
                    yearRules: [v => !!v || 'Año es requerido']
                },
                experience:{
                    name_institutionRules: [v => !!v || 'Institución es requerido'],
                    subjectRules: [v => !!v || 'Materia es requerido'],
                    yearRules: [v => !!v || 'Año es requerido'] 
                },
                cellphoneRules:[
                    v => !!v || 'Teléfono es requerido',
                    v => (v && v.length >= 8 && v.length <= 8) || 'Teléfono debe tener un minimo y maximo de 8 caracteres'
                ],
                workphoneRules:[
                    v => !!v || 'Teléfono de trabajo es requerido',
                    v => (v && v.length >= 8 && v.length <= 8) || 'Teléfono debe tener un minimo y maximo de 8 caracteres'
                ],
                homephoneRules:[
                    v => !!v || 'Teléfono de casa es requerido',
                    v => (v && v.length >= 8 && v.length <= 8) || 'Teléfono debe tener un minimo y maximo de 8 caracteres'
                ],
                addressRules:[
                    v => !!v || 'Dirección requerido'
                ]
            },
            newGeneralInfo:{
                name: '',  
                last_name: '',
                DUI: '',
                NIT: '',
                date_birth: null,
                profession:{
                    name_profession: '',
                    institution: '',
                    year:null,
                },
                mastery:{
                    name_mastery:'',
                    institution: '',
                    year: null,
                },
                characteristic: [],
                experience:{
                    name_institution: '',
                    subject: '',
                    year: null,
                    
                },
                cellphone: null,
                workphone: null,
                homephone: null,
                address:'',
            }
        }
    },
    methods:{
        validate(){
            this.$refs.generalForm.validate()
        },
        newGeneralInformation(){
            this.axios.post('general/new-inscripcion', this.newGeneralInfo)
                .then((res) => {
                    let id = res.data.personInfo[0]._id
                    router.push(`/evaluation/${id}`)
                }).catch((err) => {
                    console.log(err.response)
                })
        }

    }
}
</script>